*{
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
    width: 100vw;
    background-image: linear-gradient(to right, #051421,#042727);
}
.header{
    color: white;
    display: flex;
    align-items: center;
    padding: 20px;
    justify-content: space-between;
    overflow-x:scroll;
}
/* Hide scrollbar for Chrome, Safari and Opera */
.header::-webkit-scrollbar {
    display: none;
  }

  /* Hide scrollbar for IE, Edge and Firefox */
  .header {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
  }
.header__icons{
    display: flex;
    align-items: center;
    text-align: center;
}
.header__icon{
    display: flex;
    flex-direction: column;
    width: 100px;
    cursor:pointer
}
.header__icon > .MuiSvgIcon-root{
    margin-left: auto;
    margin-right: auto;
    font-size: 30px;
}
.header__icon > p{
    display: none;
    text-transform: uppercase;
    letter-spacing: 3px;
    margin-left: auto;
    margin-right: auto;
    font-size: small;
}
.header__icon:hover > p{
    display: inline-flex;
}
.header__icon--active > p{
    display: inline-flex !important;
}
.header > img{
    object-fit: contain;
    height: 80px;
}
.nav{
    display: flex;
    color:white;
    overflow-x:scroll;
    padding:10px;
    padding-left:40px;
}
/* Hide scrollbar for Chrome, Safari and Opera */
.nav::-webkit-scrollbar {
    display: none;
  }

  /* Hide scrollbar for IE, Edge and Firefox */
  .nav {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
  }
  .nav > h2{
      cursor:pointer;
      margin-left: 30px;
      margin-right: 30px;
      font-size: 500;
      opacity:0.7;
      transition: -webkit-transform 50ms ease-out;
      transition: transform 50ms ease-out;
      transition: transform 50ms ease-out, -webkit-transform 50ms ease-out;
  }
  .nav > h2:hover{
      opacity:1;
      -webkit-transform:scale(1.2);
              transform:scale(1.2);


  }
.results > div{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.videoCard{
color:white;
width: 500px;
height: 400px;
padding:20px;
transition: -webkit-transform 100ms ease-in;
transition: transform 100ms ease-in;
transition: transform 100ms ease-in, -webkit-transform 100ms ease-in;
}
.videoCard:hover{
    -webkit-transform: scale(1.09);
            transform: scale(1.09);
}
.videoCard > img{
    height: 300px;
    object-fit: contain;
    width: 500px;
}
.videoCard__stats{
    display:none;
}
.videoCard:hover > .videoCard__stats{
    display:inline;
}

.videCard > h2{
    padding-top: 10px;
}

